@charset "UTF-8";
body {
  font-family: Arial;
  color: #262626; }

.banner {
  font-family: Calibri;
  font-family: Arial;
  width: 700px;
  display: flex;
  position: relative; }
  .banner__header {
    min-width: 410px;
    overflow: hidden;
    position: relative; }
  .banner__down-title {
    z-index: 3;
    position: absolute;
    bottom: 25px;
    left: 25px;
    font-size: 18px;
    font-weight: 600;
    color: white;
    line-height: 150%; }
  .banner__title {
    text-align: right;
    font-size: 18px;
    font-weight: 600;
    color: black;
    position: absolute;
    top: 35px;
    left: 25px;
    z-index: 3; }
  .banner__sub-title {
    margin-top: 3px;
    z-index: 3;
    font-size: 18px;
    font-weight: 600;
    color: black; }
  .banner__item-list {
    margin-top: 20px; }
    .banner__item-list p {
      font-weight: 600;
      font-size: 90%; }
  .banner__image {
    top: 0;
    left: 0;
    z-index: -1;
    line-height: 0;
    height: 100%; }
    .banner__image img {
      max-width: initial;
      min-width: 100%;
      min-height: 100%; }
  .banner__select {
    cursor: pointer;
    transition: all ease-in-out 0.5s;
    padding: 3px;
    text-transform: uppercase;
    border: 1px solid #3399ff;
    font-weight: 600;
    font-size: 12px;
    margin-top: 8px; }
    .banner__select:first-child {
      margin-top: 0px; }
    .banner__select--active {
      background-color: #b6dbff; }
  .banner__content {
    position: relative;
    padding: 17px;
    background-color: white;
    min-width: 240px;
    padding-bottom: 50px; }
    .banner__content--step4 {
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .banner__content--step4 p {
        font-size: 15px;
        color: black; }
    .banner__content--step0 p {
      line-height: 150%; }
  .banner__button-close {
    cursor: pointer;
    position: absolute;
    right: 5px;
    top: 5px;
    width: 11px;
    height: 11px;
    z-index: 13; }
    .banner__button-close svg {
      position: absolute;
      top: 0;
      left: 0; }
  .banner__boon {
    float: left;
    color: #777777;
    font-weight: 600;
    font-size: 90%; }
  .banner__down-wrapper {
    margin-top: 25px;
    position: absolute;
    bottom: 0px;
    margin-bottom: 15px;
    width: calc(100% - 34px);
    box-sizing: border-box; }
  .banner__button-next {
    float: right;
    color: black;
    font-weight: 600;
    cursor: pointer; }
    .banner__button-next--send {
      padding: 5px;
      background-color: #8dc6ff;
      color: #3e3e3e;
      font-size: 13px;
      float: left;
      border: initial; }
  .banner__button-wrapper {
    position: absolute;
    bottom: 35px; }
    .banner__button-wrapper--theme--first:before {
      content: "Считаем?";
      margin-right: 15px;
      color: #5c5c5c;
      font-weight: 600;
      font-size: 13px; }
    .banner__button-wrapper--theme--first .banner__button-close, .banner__button-wrapper--theme--first .banner__button-next {
      float: initial;
      font-weight: 600;
      font-size: 13px;
      min-width: 30px;
      text-align: center;
      display: inline-block;
      cursor: pointer;
      position: relative;
      text-transform: uppercase;
      color: #3e3e3e;
      width: initial;
      height: initial;
      top: initial;
      padding: 5px;
      background-color: #8dc6ff; }
      .banner__button-wrapper--theme--first .banner__button-close:last-child, .banner__button-wrapper--theme--first .banner__button-next:last-child {
        margin-left: -4px; }
    .banner__button-wrapper--theme--first .banner__button-close {
      background-color: #ffc8c8; }
  .banner__input {
    color: black;
    padding-left: 6px;
    height: 24px;
    margin: 0;
    box-sizing: border-box;
    border-radius: 0;
    border: solid 1px #929292;
    width: 100%; }
    .banner__input--exception {
      border: solid 1px #ffa0a0 !important; }
    .banner__input:focus {
      border: solid 1px #3399ff;
      border-radius: 0;
      outline: none; }
  .banner__holder {
    box-sizing: border-box;
    background-color: #e1e1e1;
    color: #686868;
    display: inline-block;
    border: solid 1px #929292;
    height: 24px;
    line-height: 24px;
    padding-left: 6px;
    margin-left: 5px;
    min-width: 44px;
    font-size: 70%;
    font-weight: 600; }
    .banner__holder--power {
      line-height: 13px; }
  .banner__row {
    display: none;
    width: 100%;
    vertical-align: center;
    align-content: center;
    margin-top: 5px; }
    .banner__row:first-child {
      margin-top: 0; }
    .banner__row--visible {
      display: flex; }
    .banner__row--offset--top {
      margin-top: 30px; }
  .banner__accept {
    opacity: 0;
    height: 24px;
    z-index: 13;
    width: 15px;
    position: absolute;
    left: 100%;
    margin-left: -20px;
    top: 50%;
    transform: translateY(-50%); }
    .banner__accept--select {
      margin-left: -35px; }
    .banner__accept img {
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%); }
  .banner__input-wrapper {
    position: relative;
    width: 100%; }
    .banner__input-wrapper--accept .banner__accept {
      opacity: 1; }
  @media (max-width: 700px) {
    .banner {
      display: block;
      transform: initial;
      margin-top: initial;
      width: 100%; }
      .banner__header {
        height: 200px; }
      .banner__button-wrapper {
        position: initial; }
      .banner__content--step3 .banner__boon {
        display: none; }
      .banner__down-wrapper {
        display: block;
        position: relative;
        overflow: hidden;
        width: 100%; }
      .banner__header {
        min-width: initial;
        width: 100%; }
      .banner__content {
        padding-bottom: 5px; } }

span.yellow {
  background-color: #ffe74d;
  color: black;
  display: inline;
  padding: 8px;
  line-height: 190%; }
  span.yellow:last-child {
    margin-right: -5px; }

span.blue {
  padding: 5px;
  background-color: #8dc6ff;
  margin-left: -5px;
  color: black;
  display: inline;
  line-height: 150%; }

.hide {
  display: none; }

span.red {
  color: red;
  font-size: 70%; }

.pop-up {
  z-index: 9999999999999999999;
  background-color: rgba(0, 0, 0, 0.91);
  opacity: 0;
  display: none;
  transition: all ease-in-out 0.5s;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0; }
  .pop-up--active {
    display: block !important;
    opacity: 1; }
  .pop-up__button {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 15px; }
  .pop-up__content-wrapper {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    overflow-y: scroll; }
  @media (max-width: 700px) {
    .pop-up {
      display: none;
      margin-top: 0; }
      .pop-up__content-wrapper {
        display: block;
        min-width: initial; } }
